import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AbstractControl } from '@angular/forms'
import { ValidationService } from '../_services/Validation.service';
import { Personne, IPersonne } from '../models/personne.model';
import { PersonneService } from '../_services/personne.service';
import { ActivatedRoute } from '@angular/router';



@Component({
  selector: 'app-formulaire',
  templateUrl: './FormulaireUpdate.component.html',
  styleUrls: ['./formulaire.component.css']
})

export class FormulaireUpdateComponent implements OnInit {
 
  submitted = false;

  registerForm = this.formBuilder.group({
    id: [undefined],
    title: ['', Validators.required],
    firstname: ['', Validators.required],
    date: ['', Validators.required],
    acceptTerms: [false, Validators.requiredTrue],
    email: ['', [Validators.required, Validators.email]],
    password: ['', [Validators.required, Validators.minLength(6)]],
    
     /*
     confirmPassword: ['', Validators.required],
      lastName: ['', Validators.required],
      tel: ['', [Validators.required,ValidationService.numberValidator]],
  */
});
  constructor(private formBuilder: FormBuilder,
    private personneservice: PersonneService,
    private route: ActivatedRoute) { }


  ngOnInit() {
  
  
  if(this.route.snapshot.paramMap.get('id')){
    this.personneservice.getOne(this.route.snapshot.paramMap.get('id')).subscribe(
      (data:any)=>{
        console.log(data)
        this.updateForm(data);
      }
    );
   
  }
    ///////
    var pageHeader = $('.content-header').find('b');
    var adminForm = $('.admin-form');
    var buttons = adminForm.find('.button');
    var Panel = adminForm.find('.panel');
    setTimeout(function () {
      adminForm.addClass('theme-primary');
      Panel.addClass('panel-primary');
      pageHeader.addClass('text-primary');


      buttons.removeClass().addClass('button btn-primary');
    }, 800);
    //////
  }

 
  private createFromForm(): IPersonne {
  
    return {
      ...new Personne(),
      id: this.registerForm.get(['id'])!.value,
      firstname: this.registerForm.get(['firstname'])!.value,
      title: this.registerForm.get(['title'])!.value,
      date: this.registerForm.get(['date'])!.value,
      email: this.registerForm.get(['email'])!.value,
      password: this.registerForm.get(['password'])!.value,
      acceptTerms: this.registerForm.get(['acceptTerms'])!.value,

    };
  }
  updateForm(personne: IPersonne): void {
    this.registerForm.patchValue({
      id: personne.id,
      firstname: personne.firstname,
      title: personne.title,
      date: personne.date
    });
  }
  get f() { return this.registerForm.controls; }

    onSubmit() {
        this.submitted = true;

        // stop here if form is invalid
        if (this.registerForm.invalid) {
            return;
        }
        
        const personne = this.createFromForm();
        console.log(personne);
        if (personne.id != null) {
          this.personneservice.update(personne.id,personne).subscribe(
            ()=>{
              console.log("cool");
              this.retour();
            }
        )        
        } else {
          console.log(personne);
          this.personneservice.insert(personne).subscribe(
              (data)=>{
                this.retour();

              }
          )        
        }
        // display form values on success
      //  alert('SUCCESS!! :-)\n\n' + JSON.stringify(this.registerForm.value, null, 4));
    }
    retour(){
      window.history.back()
    }
    onReset() {
        this.submitted = false;
        this.registerForm.reset();
        this.retour();
    }
}
