import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { IPersonne } from '../models/personne.model';
import { TokenStorageService } from './token-storage.service';


type EntityResponseType = HttpResponse<IPersonne>;
type EntityArrayResponseType = HttpResponse<IPersonne[]>;
const httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };
  
@Injectable({ providedIn: 'root' })
export class PersonneService {
  public resourceUrl = 'http://localhost:8080/pers';

  constructor(protected http: HttpClient,private tokenStorage: TokenStorageService) {}

  getAll(){
    return this.http.get(this.resourceUrl);
  }

 
  insert(data){
    
    return this.http.post(this.resourceUrl,data);
  }

  delete(id){
    return this.http.delete(this.resourceUrl+'/'+id);
  }

  getOne(id){
    return this.http.get(this.resourceUrl+'/'+id);
  }
  update(id,data){
    return this.http.put(this.resourceUrl+'/'+id,data);
  }
}
